import { CurrencyAmount, Price } from '@uniswap/sdk'
import React, { useContext, useState } from 'react'
import { ThemeContext } from 'styled-components'
import { Text } from 'rebass'
import { Repeat } from 'react-feather'
import { TYPE } from '../../theme'
import { useTranslation } from 'react-i18next'
import { formatExecutionPriceExtend } from '../../utils/prices'
import { StyledBalanceMaxMini } from '../swap/styleds'
import { RowBetween, RowFixed, AutoRow } from 'components/Row'
import QuestionHelper from 'components/QuestionHelper'
import { AutoColumn } from 'components/Column'
import { ButtonError } from 'components/Button'

export default function LiquidityModalFooter({
  ePrice,
  inputAmount,
  outputAmount,
  onConfirm,
  showEstimateValue,
  showEstimateLabel
}: {
  ePrice?: Price
  inputAmount?: CurrencyAmount
  outputAmount?: CurrencyAmount
  onConfirm: () => void
  showEstimateLabel: boolean
  showEstimateValue: boolean
}) {
  const { t } = useTranslation()
  const [showInverted, setShowInverted] = useState<boolean>(false)
  const theme = useContext(ThemeContext)
  return (
    <>
      {/*兑换详情*/}
      <AutoColumn gap="0px" style={{ backgroundColor: '#ededed', padding: '1rem', color: '#333333' }}>
        <RowBetween align="center">
          <Text fontWeight={400} fontSize={14} color={theme.text7}>
            {t('Price')}
          </Text>
          <Text
            fontWeight={300}
            fontSize={14}
            color={theme.text7}
            style={{
              justifyContent: 'center',
              alignItems: 'center',
              display: 'flex',
              textAlign: 'right',
              paddingLeft: '10px'
            }}
          >
            {formatExecutionPriceExtend(ePrice, inputAmount, outputAmount, showInverted)}
            <StyledBalanceMaxMini onClick={() => setShowInverted(!showInverted)}>
              <Repeat size={14} />
            </StyledBalanceMaxMini>
          </Text>
        </RowBetween>
        {/* 预计占比 */}
        {showEstimateValue ? (
          <RowBetween>
            <RowFixed>
              <TYPE.black color={theme.text8} fontSize={14} fontWeight={400}>
                {t('Share of Pool')}
              </TYPE.black>
              <QuestionHelper text={t('The difference between the market price and your price due to trade size.')} />
            </RowFixed>
            <Text
              fontWeight={300}
              fontSize={14}
              color={theme.text7}
              style={{
                justifyContent: 'center',
                alignItems: 'center',
                display: 'flex',
                textAlign: 'right',
                paddingLeft: '10px'
              }}
            >
              11111
            </Text>
          </RowBetween>
        ) : null}
      </AutoColumn>

      <AutoRow>
        {/* 兑换预估提醒 */}
        <AutoColumn justify="flex-start" gap="sm" style={{ padding: '12px 0 0 0px' }}>
          {showEstimateLabel ? (
            <TYPE.italic textAlign="left" style={{ width: '100%' }}>
              <span
                dangerouslySetInnerHTML={{
                  __html: t(
                    'The quantity you will get is estimated. If the price changes more than x, your transaction will be cancelled',
                    {
                      percent: 0.5
                    }
                  )
                }}
              ></span>
            </TYPE.italic>
          ) : null}
        </AutoColumn>
        <ButtonError
          onClick={onConfirm}
          disabled={false}
          error={false}
          style={{ margin: '10px 0 0 0' }}
          id="confirm-liquidity-send"
        >
          <Text fontSize={20} fontWeight={500}>
            {t('Done')}
          </Text>
        </ButtonError>
      </AutoRow>
    </>
  )
}
