import { AutoColumn } from '../Column'
import { RowBetween, RowFixed } from '../Row'
import CurrencyLogo from '../CurrencyLogo'
import { TruncatedText } from '../swap/styleds'
import { Text } from 'rebass'
import ArrowTailDown from '../../assets/images/arrow-tail-down.png'
import React, { useCallback, useContext } from 'react'
import { ThemeContext } from 'styled-components'
import DoubleCurrencyLogo from '../DoubleLogo'
import { useCurrency } from '../../hooks/Tokens'
import { LiquidityFlow } from '../../state/pool/actions'

/**
 * 弹窗 头部 资产
 * @param flow 方向，转入 in 转出 out
 * @constructor
 */
export default function LiquidityModalHeader({ flow = LiquidityFlow.IN }: { flow?: LiquidityFlow | null }) {
  const theme = useContext(ThemeContext)
  const ethCurrency = useCurrency('ETH')
  const currenciesContent = useCallback(() => {
    return (
      <>
        <RowBetween align="flex-end">
          <RowFixed gap={'0px'}>
            <CurrencyLogo currency={ethCurrency ?? undefined} size={'24px'} style={{ marginRight: '12px' }} />
            <TruncatedText fontSize={24} fontWeight={400}>
              ETH
            </TruncatedText>
          </RowFixed>
          <RowFixed gap={'0px'} style={{ width: '50vw' }}>
            <Text
              fontSize={20}
              fontWeight={400}
              style={{ marginLeft: '10px', textAlign: 'right', width: '100%' }}
              color={theme.text7}
            >
              {0.00001}
            </Text>
          </RowFixed>
        </RowBetween>
        <RowBetween align="flex-end">
          <RowFixed gap={'0px'}>
            <CurrencyLogo currency={ethCurrency ?? undefined} size={'24px'} style={{ marginRight: '12px' }} />
            <TruncatedText fontSize={24} fontWeight={400}>
              DAYE
            </TruncatedText>
          </RowFixed>
          <RowFixed gap={'0px'} style={{ width: '50vw' }}>
            <Text
              fontSize={20}
              fontWeight={400}
              style={{ marginLeft: '10px', textAlign: 'right', width: '100%' }}
              color={theme.text7}
            >
              {33333332}
            </Text>
          </RowFixed>
        </RowBetween>
      </>
    )
  }, [ethCurrency, theme])
  const pairContent = useCallback(() => {
    return (
      <>
        <RowBetween align="flex-end">
          <RowFixed gap={'0px'}>
            <DoubleCurrencyLogo
              currency0={ethCurrency ?? undefined}
              currency1={ethCurrency ?? undefined}
              margin={true}
              size={20}
            />
            <TruncatedText fontSize={24} fontWeight={400}>
              111/111
            </TruncatedText>
          </RowFixed>
          <RowFixed gap={'0px'} style={{ width: '50vw' }}>
            <Text
              fontSize={20}
              fontWeight={400}
              style={{ marginLeft: '10px', textAlign: 'right', width: '100%' }}
              color={theme.text7}
            >
              {'0.132131201'}
            </Text>
          </RowFixed>
        </RowBetween>
      </>
    )
  }, [ethCurrency, theme])
  return (
    <AutoColumn gap={'md'} style={{ marginTop: '20px' }}>
      {flow === LiquidityFlow.IN ? currenciesContent() : pairContent()}
      <RowFixed gap={'0 auto'}>
        <img
          src={ArrowTailDown}
          alt={'ArrowTailDown'}
          style={{ marginLeft: '4px', maxWidth: '16px', height: '16px' }}
        />
      </RowFixed>
      {flow === LiquidityFlow.IN ? pairContent() : currenciesContent()}
    </AutoColumn>
  )
}
