import React, { Suspense } from 'react'
import { Route, Switch } from 'react-router-dom'
import styled from 'styled-components'
import Header from '../components/Header'
import Swap from './Swap'
import Pool from './Pool'
import AddLiquidity from 'pages/AddLiquidity'
import Settings from 'pages/Settings'
import Record from 'pages/Record'
import LeadingPool from 'components/LeadingPool'
import WithDraw from 'components/Withdraw'

const AppWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  overflow-x: hidden;
`

const HeaderWrapper = styled.div`
  ${({ theme }) => theme.flexRowNoWrap}
  width: 100%;
  justify-content: space-between;
`

const BodyWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 30px;
  align-items: center;
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 10;
  min-height: calc(100vh - 44px);
  background: ${({ theme }) => theme.bg2};

  ${({ theme }) => theme.mediaWidth.upToSmall`
    background: ${({ theme }) => theme.bg2};
  `};

  z-index: 1;
`

export default function App() {
  return (
    <Suspense fallback={null}>
      <AppWrapper>
        <HeaderWrapper>
          <Header />
        </HeaderWrapper>
          <BodyWrapper>
            <Switch>
              <Route exact strict path="/swap" component={Swap} />
              <Route exact strict path="/pool" component={Pool} />
              <Route exact strict path="/add" component={AddLiquidity} />
              <Route exact strict path="/settings" component={Settings} />
              <Route exact strict path="/record" component={Record} />
              <Route exact strict path="/leadingpool" component={LeadingPool} />
              <Route exact strict path="/withdraw" component={WithDraw} />
            </Switch>
          </BodyWrapper>
      </AppWrapper>
    </Suspense>
  )
}
