import { ConfirmationModalContent } from '../TransactionConfirmationModal'
import React, { useCallback } from 'react'
import LiquidityModalHeader from './LiquidityModalHeader'
import LiquidityModalFooter from './LiquidityModalFooter'
import Modal from '../Modal'
import { LiquidityFlow } from '../../state/pool/actions'

/**
 *
 * @param isOpen 打开状态
 * @param onDismiss 关闭事件
 * @param onConfirm 提交事件
 * @param showEstimateValue 预占比 值显示状态
 * @param showEstimateLabel 预占比 提示显示状态
 * @param flow
 * @constructor
 */
export default function ConfirmLiquidityModal({
  isOpen,
  onDismiss,
  onConfirm,
  showEstimateValue,
  showEstimateLabel,
  flow,
  title
}: {
  isOpen: boolean
  onDismiss: () => void
  onConfirm: () => void
  showEstimateValue: boolean
  showEstimateLabel: boolean
  flow: LiquidityFlow
  title: string
}) {
  // 获取两个币种信息和币对信息
  const modalHeader = useCallback(() => {
    return <LiquidityModalHeader flow={flow} />
  }, [flow])

  const modalBottom = useCallback(() => {
    return (
      <LiquidityModalFooter
        onConfirm={onConfirm}
        showEstimateValue={showEstimateValue}
        showEstimateLabel={showEstimateLabel}
      />
    )
  }, [onConfirm, showEstimateLabel, showEstimateValue])

  return (
    <Modal isOpen={isOpen} onDismiss={onDismiss} maxHeight={90}>
      <ConfirmationModalContent
        title={title}
        onDismiss={onDismiss}
        topContent={modalHeader}
        bottomContent={modalBottom}
      />
    </Modal>
  )
}
